<template>
  <div class="container">
    <div class="notes">
      <div class="notes__top">
        <h2 class="notes__title"> {{ words.appbartitle[lang] }}</h2>
        <button class="notes__btn" @click="grid = !grid">
          <img src="@/assets/img/grid.svg" alt="" v-if="grid" />
          <img src="@/assets/img/list.svg" alt="" v-else />
          <span>{{ grid ? words.grid[lang] : words.list[lang] }}</span>
        </button>
      </div>
      <div class="notes__list" :class="{ active: grid }">
        <NoteItem 
        v-for="note, i in notes"
         :key="note.id"
         :note="note"
         :lang="lang"
         @delNote="$emit('delNote', i)"
         @editNote="$emit('editNote',note.id)"
         />
      </div>
    </div>
  </div>
</template>

<script>
import NoteItem from "@/components/NoteItem";
export default {
  components: {
    NoteItem,
  },
  data() {
    return {
      grid: false,
    };
  },
  props: {
    notes: Array,
    lang:String
  },
  inject:['words']
};
</script>

